@charset "UTF-8";
*::after,
*::before {
  box-sizing: border-box; }

body {
  padding: 0;
  margin: 0 auto;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: normal; }

.visually-hidden:not(:focus):not(:active) {
  /*Позиционирование*/
  position: absolute;
  /*Блочная модель*/
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  /*Разное*/
  white-space: nowrap;
  clip-path: inset(100%);
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

